import React, { useEffect, useState, useCallback } from "react";
import "./Chat-Form.css";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import EmojiEmotionsIcon from "@material-ui/icons/EmojiEmotions";
import AttachFile from "@material-ui/icons/AttachFile";
import { makeStyles } from "@material-ui/core/styles";
import { roundTextFieldStylesHook } from "@mui-treasury/styles/textField/round";
import TextField from "@material-ui/core/TextField";
import ChatSocketServer from "./utils/chatSocketServer";
import EmojiPicker from "./EmojiPicker";
import ImagePicker from "./File-Preview";
import "./Chat-Form.css";
import "./Emoji-Popup.css";

const ChatPage = ({
  messageList,
  handleChange,
  newMessage,
  userId,
  handleSend,
  setMessageList,
  selectedChat,
  setChats,
  chatList,
}) => {
  const [userTyping, setUserTyping] = useState({
    userId: "",
    userAction: "",
  });

  const handleMessagesAdd = useCallback(
    (data) => {
      let newList;
      let chats;

      if (data._doc) {
        newList = messageList.filter(
          (message) => message._id !== data._doc._id
        );
        setMessageList([{ ...data._doc, chatId: data.chatId }, ...newList]);
        chats = chatList.filter((chat) => {
          if (chat.chatId === data.chatId) {
            chat.messages[0] = data._doc;
            chat.chatId = data.chatId;
          }
          return chat;
        });
      } else {
        newList = messageList.filter((message) => message._id !== data._id);
        setMessageList([{ ...data, chatId: data.chatId }, ...newList]);
        chats = chatList.filter((chat) => {
          if (chat.chatId === data.chatId) {
            chat.messages[0] = data;
            chat.chatId = data.chatId;
          }
          return chat;
        });
      }

      setChats([...chats]);
    },
    [chatList, messageList, setMessageList, setChats]
  );

  const handleMessagesUpdate = useCallback(
    (data) => {
      const newList = messageList.filter((message) => {
        if (message._id === data._id) {
          message.state = data.state;
        }
        return message;
      });
      setMessageList([...newList]);

      const chats = chatList.filter((chat) => {
        if (chat.chatId === data.chatId) {
          chat.messages[0] = data;
          chat.chatId = data.chatId;
        }
        return chat;
      });
      setChats([...chats]);
    },
    [setMessageList, messageList, chatList, setChats]
  );

  useEffect(() => {
    ChatSocketServer.eventEmitter.on("add-message-response", handleMessagesAdd);
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "add-message-response",
        handleMessagesAdd
      );
  }, [handleMessagesAdd]);

  useEffect(() => {
    ChatSocketServer.eventEmitter.on(
      "update-message-response",
      handleMessagesUpdate
    );
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "update-message-response",
        handleMessagesUpdate
      );
  }, [handleMessagesUpdate]);

  useEffect(() => {
    ChatSocketServer.eventEmitter.on("set-typing-response", (data) => {
      setUserTyping(data);
      setTimeout(() => {
        setUserTyping({
          userAction: "",
        });
      }, 1000);
    });
  }, [setUserTyping]);

  const displayMessagePost = (messageList) => {
    if (!messageList?.length) return null;

    return messageList.map((message) => {
      if (selectedChat.chatId && selectedChat.chatId === message.chatId) {
        if (message.state === "DELIVERED" && message.receiverId === userId) {
          const newList = messageList.filter(
            (data) => data._id !== message._id
          );
          setMessageList([...newList, { ...message, state: "READ" }]);
          ChatSocketServer.updateMessage({
            ...message,
            state: "READ",
            chatId: selectedChat.chatId,
          });
        }
      }

      if (message.error) return <div>Server error: Failed to send</div>;
      return (
        <div key={message._id} className="message-post__display">
          {message.files[0] ? (
            message.files[0].type?.includes("image") ? (
              <div>
                <img
                  src={message.files[0].localFileUrl || message.files[0].url}
                  alt="upload"
                />
                <div>
                  <p>{message.files[0].caption}</p>
                </div>
              </div>
            ) : (
              <a
                target="_blank"
                rel="noopener noreferrer"
                href={message.files[0].localFileUrl || message.files[0].url}
              >
                {message.files[0].originalName
                  ? message.files[0].originalName
                  : message.files[0].name}
              </a>
            )
          ) : (
            <p>{message.message} </p>
          )}
          {message.receiverId !== userId ? message.state : ""}
        </div>
      );
    });
  };

  const toggleEmoji = () => {
    let emojiView = document.getElementById("emojiPicker");
    emojiView.classList.toggle("show");
  };

  const toggleImagePicker = () => {
    let imageView = document.getElementById("imagePicker");
    imageView.classList.toggle("show");
  };

  const pickEmoji = (data) => {
    handleChange(`${newMessage}${data.native}`);
  };

  return (
    <div id="chat-form">
      {selectedChat.businessId && (
        <div className="messages-container">
          <div>{displayMessagePost(messageList)}</div>
          {selectedChat.businessId === userTyping.userId && (
            <div>{`${userTyping.userAction}...`}</div>
          )}
        </div>
      )}
      {selectedChat.businessId && (
        <div>
          <div className="popup">
            <AttachFile onClick={toggleImagePicker} />
            <div className="popuptext" id="imagePicker">
              <ImagePicker
                toggleImagePicker={toggleImagePicker}
                send={handleSend}
              />
            </div>
          </div>
          <div className="popup">
            <EmojiEmotionsIcon onClick={toggleEmoji} />
            <div className="popuptext" id="emojiPicker">
              <EmojiPicker pickEmoji={pickEmoji} />
            </div>
          </div>
          <TextField
            placeholder={"Type a message"}
            margin={"small"}
            style={{ height: 33, width: "70%" }}
            // InputProps={{ classes: inputBaseStyles, disableUnderline: true }}
            value={newMessage}
            onChange={(e) => handleChange(e.target.value)}
          />
          <Button style={{ width: "10%" }} onClick={() => handleSend()}>
            Send
          </Button>
        </div>
      )}
      <div />
    </div>
  );
};

export default ChatPage;
