import React, { useEffect, useState } from "react";
import Badge from "@material-ui/core/Badge";
import MessageIcon from "@material-ui/icons/Message";
import ChatSocketServer from "./utils/chatSocketServer";
import { Link } from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
import { Menu } from "@material-ui/core";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

const useStyles = makeStyles((theme) => ({
  button: {
    display: "block",
    marginTop: theme.spacing(2),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

const Notifications = () => {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [state, setState] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);
  const [anchorEl, setAnchorEl] = useState();

  const [userId] = useState(
    JSON.parse(localStorage.getItem("posts"))?.posts?.profile[0]?._id
  );

  const [userName] = useState(
    JSON.parse(localStorage.getItem("posts"))?.posts?.profile[0]?.businessName
  );

  useEffect(() => {
    ChatSocketServer.establishSocketConnection(userId);
    ChatSocketServer.receiveNotifications();
    ChatSocketServer.getNotifications(userId);
  }, [userId]);

  useEffect(() => {
    const handleNotification = (data) => {
      if (data.notifications?.list) {
        setState(data.notifications.list);
        if (data.notifications.unreadCount[0])
          setUnreadCount(data.notifications.unreadCount[0]?.unread);
      } else setState([...state, data]);
    };
    ChatSocketServer.eventEmitter.on(
      "notification-response",
      handleNotification
    );
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "notification-response",
        handleNotification
      );
  }, [state]);

  useEffect(() => {
    const handleNotificationAdd = (data) => {
      setState([...state, data]);
      console.log(data);
      setUnreadCount(unreadCount + 1);
    };

    ChatSocketServer.eventEmitter.on(
      "send-notification-response",
      handleNotificationAdd
    );
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "send-notification-response",
        handleNotificationAdd
      );
  }, [unreadCount, state]);

  useEffect(() => {
    const handleMessageAdd = (data) => {
      console.log(data);
      if (data.senderId !== userId) setUnreadCount(unreadCount + 1);
    };

    ChatSocketServer.eventEmitter.on("add-message-response", handleMessageAdd);
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "add-message-response",
        handleMessageAdd
      );
  }, [userId, unreadCount]);

  const handleClose = (id, readState) => {
    if (readState === "DELIVERED")
      ChatSocketServer.updateNotification({
        id,
        target: userId,
        from: userName,
      });
    setOpen(false);
    const updated = state.filter((x) => {
      if (x._id === id) {
        x.state = "READ";
      }
      return x;
    });

    setState([...updated]);
  };

  const listNotifications = (list) => {
    if (!list[0]) return <div>No notifications</div>;
    return list.map((item) => {
      const { type, message, state: readState, _id } = item;
      if (type === "MESSAGE")
        return (
          <Link key={_id} to="/inbox">
            <MenuItem onClick={() => handleClose(_id, readState)}>
              {`${message}`} | {readState === "DELIVERED" ? "D" : "R"}
            </MenuItem>
          </Link>
        );
      return <MenuItem onClick={() => handleClose(item)}>{message}</MenuItem>;
    });
  };

  const recordButtonPosition = (event) => {
    setAnchorEl(event.currentTarget);
    setOpen(!open);
  };

  return (
    <div>
      <div onClick={recordButtonPosition}>
        <Badge
          style={{ top: 28, left: 71 }}
          badgeContent={unreadCount}
          max={50}
          color="primary"
        >
          <MessageIcon />
        </Badge>
      </div>
      <FormControl className={classes.formControl}>
        {/* <InputLabel id="demo-controlled-open-select-label">Age</InputLabel> */}
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={open}
          onClose={handleClose}
        >
          {listNotifications(state)}
        </Menu>
      </FormControl>
    </div>
  );
};

export default Notifications;
