class About extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      item: [],
    };
  }
  componentWillMount() {
    this.setState({
      item: this.props.posts.find(
        ({ _id }) => _id === this.props.match.params.id
      ),
    });
  }

  createChat = async () => {
    const { businessName, _id } = this.state.item;
    this.props.history.push({
      pathname: "/inbox",
      selectedChatUser: {
        businessId: _id,
        title: businessName,
        imageAlt: businessName,
        chatId: "",
        messages: [],
      },
    });
  };

  render() {
    return (
      <div className="cover">
        <div class="aboutcard">
          <div class="aboutheader">
            <div class="rowtop">
              <p
                style={{
                  "font-size": "25px",
                  paddingLeft: 13,
                  paddingTop: 0,
                  height: 2,
                }}
              >
                {" "}
                {this.state.item.businessName}
              </p>
              <a onClick={() => this.createChat()}>
                <ChatIcon style={{ marginTop: "35px", paddingLeft: 10 }} />
              </a>
            </div>
            <p
              style={{
                "font-size": "15px",
                paddingLeft: 13,
                height: 0,
                width: "70%",
              }}
            >
              {this.state.item.businessDescription}
            </p>
            <p style={{ "font-size": "14px", height: 6, paddingLeft: 13 }}>
              {" "}
              Stage: Week 1|{this.state.item.businessIndustry}
            </p>
            <Rating
              precision={0.5}
              style={{ paddingLeft: 13 }}
              name="size-medium"
              value={3.4}
              size="small"
              readOnly
            />
          </div>
          <div class="photo">
            <Avatar
              style={{ height: 95, width: 95, top: 34 }}
              src={"G"}
              alt={"R"}
            />
          </div>

          <div class="body">
            <GridList cellHeight={"auto"} style={{ overflow: "auto" }} cols={1}>
              <GridListTile key={this.state.item._id}>
                <Typography
                  style={{
                    paddingLeft: 22,
                    width: "95%",
                    "font-size": "14px",
                    marginBottom: 10,
                  }}
                >
                  {this.state.item.aboutBusiness}
                </Typography>
              </GridListTile>

              <GridListTile>
                <Typography
                  style={{
                    width: "80%",
                    height: "1vh",
                    "font-size": "15px",
                    marginBottom: 10,
                  }}
                ></Typography>
              </GridListTile>
            </GridList>
          </div>
          <div class="button">
            <Link
              style={{ textDecoration: "none" }}
              to={`/Ventures/Rate/${this.state.item._id}`}
            >
              <StyledButton color="secondary" size="medium">
                Rate Business
              </StyledButton>
            </Link>
          </div>
        </div>
        <div>
          <Filtercard />
        </div>
      </div>
    );
  }
}

About.propTypes = {
  fetchPosts: PropTypes.func.isRequired,
  posts: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  posts: state.posts.items,
});

export default connect(mapStateToProps, { fetchPosts })(About);
