import { fetchPosts,fetchProfile } from '../../storage/actions/postActions';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Typography from '@material-ui/core/Typography';

function youtubeLinkParser(url) {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/;
    var match = url.match(regExp);
    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return null;
    }
} 

function BoardHeader(){
   return(
     <div className="ventureheader">
         <p style={{fontSize: '26px',height:3,paddingLeft:20}}>Venture Board</p>
                 <Divider />
      </div>
    )
}
function BoardList(props){
  const list= props.list;
  
   return(
     <div className='notificationlist'>
          <GridList cellHeight={"auto"} style={{overflow: 'scroll',width:"100%"}} cols={3}>
        {list.map(({businessName,businessDescription,_id})=> (

            <ListItem style={{width:"32%",marginLeft:10}} component={Link} to={{pathname : `/Ventures/${_id}` ,data:list}}   button key={businessName}>
              <Avatar src={"R"} alt="R" />
              <ListItemText style={{marginLeft:5}} primary={businessName} secondary={businessDescription}/>
              <Badge  badgeContent={12} max={9} color="primary">
              <FavoriteIcon />
              </Badge>
            </ListItem>

          ))}
      </GridList>
     </div>
    )   
}
function VentureBoard(props){
  return(
    <div className="ventureboard">
      <BoardHeader />
      <BoardList list={props.list}/>
    </div>
  )
}
function Content(props){
   return(
        <div className='broot'>
        <CardHeader
           title="Continue Your Journey"
        />
          <img style={{height:"21vh",marginLeft:"16%",width:"60%",}} src={props.state.image} alt="R" />
          <div className="row">
            <Typography style={{'align-self': 'flex-end',paddingTop:4,'font-size': '16px'}}>{props.state.focus}</Typography>
            <div className="newcol">
            <Typography style={{'align-self': 'flex-end',paddingTop:4,'font-size': '16px',right:0}}>{props.state.current} of 26 </Typography>
            </div>
          </div>
      </div>
    )
}
class HomePage extends React.Component {
     constructor(props) {

    super(props);



    this.state = {
       list:[],
       image:null,
       current:null,
       focus:null,
       notificationlist:[],
    };

  };

   componentWillMount() {
    

     this.setState({list:this.props.posts});
    
    var getList=JSON.parse(localStorage.getItem("posts")).posts;
         var current=JSON.parse(localStorage.getItem("current"));
         var watched=JSON.parse(localStorage.getItem("watched"));

          getList =getList.videos;
        var material= getList.find(({Week}) => Week === current);
                      this.setState({current:current,focus:material.Focus});

       var extract =material.Videos;
         var images = extract.map(x => youtubeLinkParser(x));
      images= images.map(x=> `https://img.youtube.com/vi/${x}/0.jpg`)
      this.setState({image:images[watched]})
  }
  componentDidMount(){
          this.props.fetchPosts();
          if(JSON.stringify(this.state.list) !==JSON.stringify(this.props.posts)){
             this.setState({list:this.props.posts});
          
          }
  }
   
 render(){
       const { classes } = this.props;
          const state=this.state;
          const list=state.list;
          const notifications=state.notificationlist;
          const handleClear = (e) => {
            this.setState({list:[]});

           }; 
      function NotifHeader(){

         const [anchorEl, setAnchorEl] = React.useState(null);

          const handleClick = (event) => {
            setAnchorEl(event.currentTarget);
          };

          const handleClose = () => {
            setAnchorEl(null);
          };
         
           return(
             <div className="notifheader">
                <div style={{width:"84%"}}>
                 <p style={{fontSize: '25px',height:3,paddingLeft:20}}>Notifications </p>
                 </div>
                 <div >
                 <IconButton style={{top:10}} aria-label="settings" aria-haspopup="true" onClick={handleClick}>
                   
                   <MoreVertIcon />
                  </IconButton>

                       <Menu
                            id="simple-menu"
                           anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                           onClose={handleClose}
                     >
                      <MenuItem onClick={handleClear}>Clear</MenuItem>
                   </Menu>
                 </div>
              </div>
            )
        }
            function Notificationlist(){
             return(
               <div className='notificationlist'>
                  <List>
                    {notifications.map(({businessName})=> (

                      <ListItem style={{height:55}}  button key={businessName}>
                        <FiberManualRecordIcon style={{ color: 'red'}}/>
                        <Avatar src={'R'} alt="R" />
                        <ListItemText style={{marginLeft:5}} primary={businessName} secondary={"notif"}/>
                      </ListItem>

                    ))}
                  </List>
               </div>
              )
          }
          function Notifications(){
            return(
               <div className="notificationcontainer">
                 <NotifHeader/>
                 <Notificationlist />
               </div>
            )
          }
  return (
    <div>
        <div className="row" >
           <Content state={this.state} />
           <Notifications/>
        </div>
         <VentureBoard list={this.state.list} />

           </div>
  );
}
}

HomePage.propTypes = {
  posts: PropTypes.array.isRequired,
  profile: PropTypes.array.isRequired,
    fetchPosts: PropTypes.func.isRequired,

};

const mapStateToProps = state => ({
  posts: state.posts.items,
    profile:state.posts.profile

});

export default connect(mapStateToProps, { fetchPosts })(HomePage);