function Overall(props) {
  const item = props.data.find(({ _id }) => _id === props.match.params.id);
  const [value, setValue] = React.useState(0);
  const [userName] = useState(
    JSON.parse(localStorage.getItem("posts"))?.posts?.profile[0].businessName
  );

  console.log(item);

  const handleClick = (event) => {
    var overall = value * 0.5;
    finalrating = finalrating + overall;
    const data = {
      businessName: item.businessName,
      rating: `${finalrating}`,
    };

    axios({
      method: "put",
      url: `${process.env.REACT_APP_API_END}/businesses/rate`,
      data: data,
      headers: {
        "x-auth-token": localStorage.getItem("token"),
      },
    }).then((res) => {
      ChatSocketServer.sendNotification({
        type: "RATING",
        rating: data.rating,
        senderName: userName,
        receiverId: item._id,
      });
      console.log(res);
    });
  };
  return (
    <div className="cover">
      <div class="overallcard">
        <div class="aboutheader">
          <div class="rowtop">
            <p
              style={{
                "font-size": "25px",
                paddingLeft: 13,
                paddingTop: 0,
                height: 2,
              }}
            >
              {" "}
              {item.businessName}
            </p>
            <Link to="/inbox">
              <ChatIcon style={{ marginTop: "35px", paddingLeft: 10 }} />
            </Link>
          </div>
          <p
            style={{
              "font-size": "15px",
              paddingLeft: 13,
              height: 0,
              width: "70%",
            }}
          >
            {item.businessDescription}
          </p>
          <p style={{ "font-size": "14px", height: 6, paddingLeft: 13 }}>
            {" "}
            Stage: Week 1|{item.businessIndustry}
          </p>
          <Rating
            style={{ paddingLeft: 13 }}
            name="size-medium"
            precision={0.5}
            value={3.44}
            size="small"
            readOnly
          />
        </div>
        <div class="photo">
          <Avatar
            style={{ height: 95, width: 95, top: 34 }}
            src={"G"}
            alt={"R"}
          />
        </div>
        <div class="body">
          <div className="rate">
            <p
              style={{
                "font-size": "15px",
                paddingTop: 4,
                paddingLeft: 18,
                width: "75%",
              }}
            >
              Thank you for rating this business, please rate the overall
              business.
            </p>
          </div>
          <div className="rate">
            <p
              style={{
                "font-size": "15px",
                paddingLeft: 18,
                top: 0,
                width: "40%",
              }}
            >
              Overall rating
            </p>
            <Rating
              style={{ paddingTop: 15 }}
              name="size-medium"
              value={value}
              precision={0.5}
              size="medium"
              onChange={(event, newValue) => {
                setValue(newValue);
              }}
            />
          </div>
        </div>
        <div class="submit">
          <Link style={{ textDecoration: "none" }} to={`/Ventures`}>
            <StyledButton onClick={handleClick} size="medium">
              Submit
            </StyledButton>
          </Link>
        </div>
      </div>
    </div>
  );
}
class Ventures extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      list: [],
    };
  }
  componentDidMount() {
    console.log(this.props);
    this.setState({ list: this.props.posts });
  }

  render() {
    return (
      <div className="cover">
        <div className="flow">
          <Switch>
            <Route
              exact
              path="/Ventures"
              render={(props) => <Startups {...props} data={this.state.list} />}
            />
            <Route
              exact
              path="/Ventures/:id"
              render={(props) => <About {...props} data={this.state.list} />}
            />
            <Route
              exact
              path="/Ventures/Rate/:id"
              render={(props) => <Rate {...props} data={this.state.list} />}
            />
            <Route
              path="/ventures/overall/:id"
              render={(props) => <Overall {...props} data={this.state.list} />}
            />
          </Switch>
        </div>
      </div>
    );
  }
}
Ventures.propTypes = {
  fetchPosts: PropTypes.func.isRequired,
  posts: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  posts: state.posts.items,
});

export default connect(mapStateToProps, { fetchPosts })(Ventures);
